.navbar {
    width: 100%;
    height: 60px;
    display: flex;
    flex-direction: row;
}

.navbar .logo {
    flex: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    padding-left: 150px;
}

.navbar .logo img {
    height: 50px;
}

.navbar .nav-link {
    flex: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navbar a {
    color: #403029;
    text-decoration: none;
    margin: 20px;
    font-size: 12px;
    letter-spacing: 1px;
    font-family: roboto-mono;
    text-transform: uppercase;
    font-weight:500;
  }